<template>
  <!--/sub-heard-part-->
  <div>
  <div class="sub-heard-part">
    <ol class="breadcrumb m-b-0">
      <li><a href="/ui/#/"></a></li>
      <li class="active"></li>
    </ol>
  </div>
  <!--/sub-heard-part-->
  <!--/forms-->
  <div class="forms-main">
    <h2 class="inner-tittle"> </h2>
    <div class="graph-form">
      <div class="form-body">
        <div class="form-group">
          <label for="cron-blame"></label>
          <input type="text" class="form-control" id="cron-blame">
        </div>
          <div class="form-group">
            <label for="cron-set">*/1 * * * * *</label>
            <input type="text" class="form-control" id="cron-set">
          </div>
          <div class="form-group">
            <label for="start-time"></label>
            <input type="text" class="form-control" id="start-time" v-bind:value="datetime">
          </div>
          <div class="form-group">
            <label for="end-time"></label>
            <input type="text" class="form-control" id="end-time" value="2099-01-01 08:00:00">
          </div>
          <div class="form-group">
            <label for="command"></label>
            <input type="text" class="form-control" id="command" value="">
          </div>
          <div class="checkbox">
            <label>
              <input type="checkbox" id="cron-stop"> 
            </label>
          </div>
          <div class="checkbox">
            <label>
              <input type="checkbox" id="cron-is-mutex"> 
            </label>
          </div>
          <div class="form-group">
            <label for="remark"></label>
            <textarea class="form-control" id="remark"></textarea>
          </div>
          <button type="button" class="btn btn-default" id="do-submit"></button>
      </div>

    </div>
    <!--/forms-inner-->
    <!--//forms-inner-->
  </div>
  </div>
  <!--//forms-->
</template>
<script>
  export default {
    name: "Add",
    data: function() {
      return {
        datetime: (new Date()).Format("yyyy-MM-dd hh:mm:ss"),
      }
    },
    created: function() {
      let script = document.createElement("script");
      script.src = "./static/js/add.vue.js?t=" +  (new Date()).valueOf();
      document.body.appendChild(script)
    },
  }
</script>



